* {
  margin: 0px;
  padding: 0px;
}

.App {
  text-align: center;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  margin-top: 1em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.App-gallery {
  width:100%;
}
.App-gallery ul.characters {
  margin-top:1em;
}
.App-gallery li.character {
  display: inline-block;
  list-style: none;
  width:21%;
  margin:2%;
  cursor: pointer;
  opacity: 0.5;
}
.App-gallery li.character:hover {
  opacity: 1;
}

.App-gallery li.character img {
  max-width: 100%;
  height: auto;
  display:block;
  border-radius:5px;
}
.App-gallery li.character h3 {
  font-size: 1.2em; font-weight: bold; padding-bottom: 0.5em;
}

.App-gallery li.character.loading {
  background: url('img/loader.png') no-repeat 50% 50%;
}
.App-gallery li.character.loading img {
  visibility: hidden;
}

.skylight-dialog {text-align:left;}
.skylight-dialog h2 {font-size: 1.2em;}

.characterContent {margin-top:1em; float:left;}
.characterContent h3 {font-size: 1.8em; font-weight: bold;}
.characterContent .leftSide {float:left; width:25%;}
.characterContent img.thumbnail {
  max-width: 100%;
  height: auto;
  display:block;
}
.characterContent .rightSide {float:right; width:72%; padding-left: 3%;}
.characterContent section.detail {padding:1em; border:1px solid #ddd; margin-bottom: 1em; border-radius:2px; }
.characterContent section.detail h4 {font-size: 1.2em; font-weight: bold;}
.characterContent section.detail p {font-size: 0.8em; margin-top:0.8em;}
.characterContent section.detail ul {margin-top: 1em;}
.characterContent section.detail ul li {list-style: none; font-size: 0.8em; border-bottom: 1px solid #eee; padding-bottom: 0.3em; margin-bottom: 0.3em;}

.characterContent .scroller {
  height: 430px;
  overflow-y:auto;
}



